*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.home_div{
    width: 100%;
    /* background: brown; */
    height: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    
}

.home_text{
    width: 40%;
    height: 100%;
    /* background: chartreuse; */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    
}

.home_div .home_text h1{
    text-transform: capitalize;
    color: black;
    font-family: 'Mulish', sans-serif;
    font-size: 3rem;
    line-height: 4rem;
}

.home_div .home_text h1 span{
    color: #00cec9;
}

.home_text h3{
    font-size: 1.5rem;
    color: #535c68;
    margin-bottom: 2rem;
}

.home_text a{
    font-size: 1.5rem;
    padding: 1rem 1rem;
    border: 0.2rem solid #00cec9;;
    position: relative;
    text-decoration: none;
    right: 29%;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    border-radius: 5rem;
    color: #00cec9;;
    background:transparent;
    font-weight: 1000;
}

.home_text a:hover{
    background: #1B9CFC;
    color: #fff;
}

.home_img{
    width: 60%;
    height: 100%;
    /* background: blue; */
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;   
    -webkit-filter: drop-shadow(4px 8px 10px #000000);   
            filter: drop-shadow(4px 8px 10px #000000);
}

.home_img img{
    width: 90%;
    height: 80%;
    position: absolute;
    top: 6rem;
    -webkit-animation:  imgMove 2s linear infinite;
            animation:  imgMove 2s linear infinite;
} 



@-webkit-keyframes imgMove{
    0%{
        top: 6rem;
    }
    
    50%{
        top: 5rem;
    }
    100%{
        top: 6rem;
    }

} 



@keyframes imgMove{
    0%{
        top: 6rem;
    }
    
    50%{
        top: 5rem;
    }
    100%{
        top: 6rem;
    }

}



.contact_div{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
    display: -webkit-flex;
    display: flex;
    /* background: coral; */
    -webkit-align-items: center;
            align-items: center;
    background: #fff;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.contact_head{
    width: 50%;
    display: grid;
    place-items: center;
    /* background: cornflowerblue; */
    padding: 2rem;
    font-size: 2rem;
    margin-top: 2rem;
    color: #808e9b;

}

.contact_form{
    width: 60%;
}

.contact_form form{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* border: 0.1rem solid black; */
}

.contact_form form .input{
    width: 70%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 1rem;
}

.contact_form form .input label{
    padding-bottom: 0.5rem;
    font-size: 1.2rem;
}

.contact_form form .input input{
    outline: 0;
    border: 0.1rem solid #d2dae2;
    padding: 0.4rem;
    color:#485460 ;
}

.contact_form form .input textarea{
    resize: none;
    padding: 0.4rem;
    color:#485460 ;
    outline: 0;
    border: 0.1rem solid #d2dae2;
}

.btn{
    width: 8rem;
    height: 3rem;
    outline: 0;
    border: 0.2rem solid #d2dae2;
    border-radius: 1rem;
    
}

.btn:hover{
    background: #0fbcf9;
    color: #fff;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
    /* overflow: hidden; */

}

html{
    font-size: 62.5%;
}
body{
    display: -webkit-flex;
    display: flex;
    background: hsl(180, 74%, 95%);
    -webkit-justify-content: center;
            justify-content: center;
    width: 100vw;
    height: 100vh;
}

.nav{
    width: 90vw;
    height: auto;
    display: -webkit-flex;
    display: flex;
    /* background: red; */
    -webkit-align-items: center;
            align-items: center;
}

.nav h1{
    color: #2C3A47;
    text-transform: uppercase;
}

.active_class{
    border-bottom: 0.1rem solid black;
    color: black;
}

.nav_text{
    text-decoration: none;
    margin-right: 2rem;
    font-size: 1.5rem;
    color: #535c68; 
    font-weight: 1000!important;
}

.nav_text:hover{
    color: black;
}

.nav_list{
    text-decoration: none;
    margin-left: auto;
    padding: 1rem 1rem;
}
